@font-face {
  font-family: 'Pretendard';
  src: local('Pretendard-Regular'), url('assets/fonts/Pretendard-Regular.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Pretendard';
  src: local('Pretendard-Bold'), url('assets/fonts/Pretendard-Bold.otf') format('opentype');
  font-weight: bold;
  font-style: normal;
}

body {
  font-family: 'Pretendard', sans-serif;
}

